import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HeaderComponent } from './header.component';
import { AuthGuard } from '../auth/auth.guard';
import { HomeNotificationComponent } from './home-notification/home-notification.component';


const routes: Routes = [

    {path:'',component:HeaderComponent,canActivate:[AuthGuard],children:[
        {
        path:'notification',component:HomeNotificationComponent,canActivate:[AuthGuard]
      }
      ]},
   
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HeaderRouting {}
